import React, { useState, useEffect } from 'react';

// default
import Header from '../../components/Header';
import Footer from '../../components/Footer';
import '../../scss/common.scss';

import Banner from '../../components/HomeCatalogo/Banner';
// import Category from '../components/SideBar/Category';
import Promotion from '../../components/HomeCatalogo/Promotion';
import Release from '../../components/HomeCatalogo/Release';
import BestSeller from '../../components/HomeCatalogo/BestSeller';
import BannerAD from '../../components/HomeCatalogo/BannerAD';
import OwnBrand from '../../components/HomeCatalogo/OwnBrand';

import Modal from "react-bootstrap/Modal";
import { fetchProductsByTagHome } from '../../services/products-api';

const HomeCatalogo = ({ refreshCartFn }) => {


    const [promotions, setPromotions] = useState([]);
    const [releases, setReleases] = useState([]);
    const [ownBrands, setOwnBrands] = useState([]);
    const [bestSellers, setBestSellers] = useState([]);

    useEffect(() => {
        fetchProductsByTagHome('Promocao', 4)
            .then(result => {
                if (result.data.Data) {
                    setPromotions(result.data.Data.Dados)
                }
            })
        /* Deixado somente 1 disconnect */
        // .catch(reject => {
        //     disconnect();
        // })
        fetchProductsByTagHome('Lancamento', 4)
            .then(result => {
                if (result.data.Data) {
                    setReleases(result.data.Data.Dados)
                }
            })
        /* Deixado somente 1 disconnect */
        // .catch(reject => {
        //     disconnect();
        // })
        fetchProductsByTagHome('MarcaPropria', 4)
            .then(result => {
                if (result.data.Data) {
                    setOwnBrands(result.data.Data.Dados)
                }
            })
        /* Deixado somente 1 disconnect */
        // .catch(reject => {
        //     disconnect();
        // })
        fetchProductsByTagHome('Revista', 4)
            .then(result => {
                if (result.data.Data) {
                    setBestSellers(result.data.Data.Dados)
                }
            })
        /* Deixado somente 1 disconnect */
        // .catch(reject => {
        //     disconnect();
        // })
    }, [])

    return (
        <>
            <Header />
            <Banner />
            {/* <section className="conteudo_interno container">
                <div className="filtro-sticky d-none d-lg-block">
                    <Manufacturer />
                </div>
                <div className="cards-container">
                </div>
            </section> */}
                <Promotion destaque={promotions} />
                <Release destaque={releases} />
                <BannerAD />
                <BestSeller destaque={bestSellers} />
                <OwnBrand destaque={ownBrands} />


            <Footer />
        </>
    )
}


export default HomeCatalogo;